<template>
  <div class="successfulTrade">
    <van-nav-bar
      v-if="head"
      title="订单详情"
      fixed
      @click-left="$toPage(-1)"
      >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <van-nav-bar
      v-if="!head"
      title="订单详情"
      fixed
      @click-left="$toPage('shoppingCart')"
      >
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <div class="state">
      <span v-if="order.ostatus == 0 && order.opayway == -1">待付款</span>
      <span v-if="order.ostatus == 0 && order.opayway !== -1">您的订单已提交，请耐心等待客服审核~</span>
      <span v-if="order.ostatus == 2">等待签收</span>
      <span v-if="order.ostatus == 3">已签收</span>
      <span v-if="order.ostatus == 4 && order.asstatus !== 200">已完成</span>
      <span v-if="order.ostatus == -4">交易取消</span>
      <span
        v-if="order.ostatus == 1 && order.opayway != 4 && order.opayway != 5"
        >付款成功</span
      >
      <span
        v-if="order.ostatus == 1 && (order.opayway == 4 || order.opayway == 5)"
        >待发货</span
      >
      <span v-if="order.ostatus == -1">申请售后</span>
      <span v-if="order.ostatus == -2">售后中</span>
      <span v-if="order.asstatus == 200">完成售后</span>
    </div>
    <div class="userInfo">
      <div class="userName">
        <span>{{ order.consignee }}</span>
        <span class="tel">{{ order.contact }}</span>
        <van-icon name="arrow" />
      </div>
      <p class="storeName">{{ order.cusname }}</p>
      <div class="address">
        <img src="../../assets/goodsDetails_icon/adress.png" />
        <p>
          {{ order.areaAllName }} {{ order.cusaddress }}
        </p>
      </div>
    </div>
    <div v-if="order.ostatus == 4" class="orderInfo">
      <div class="content">
        <img src="../../assets/goodsDetails_icon/logistics.png" />
        <p>订单已完成，感谢您在一块医药购物，欢迎再次光临！</p>
        <van-icon name="arrow" />
      </div>
      <div class="time">{{ order.odate }} {{ order.otime }}</div>
    </div>
    <!-- 物流 -->
    <div v-if="order.ostatus > 0" class="orderInfo_D">
      <div class="content" @click="logistics">
        <p>配送方式：</p>
        <p>一块物流</p>
        <van-icon name="arrow" />
      </div>
      <!-- <div class="time" v-if="order.opayway == -1">
        下单后的20：25前付款，预计后天送达
      </div> -->
    </div>
    <!--  -->
    <div class="orderMode">
      <p>
        <span>支付方式： </span>
        <span>{{ payText(order.opayway) }}</span>
        <span v-if="!payText(order.opayway)">未支付</span>
      </p>
      <p>
        <span>发票类型： </span>
        <span v-if="order.invoicetype == 1">电子普通发票</span>
        <span v-if="order.invoicetype == 2">纸质普通发票</span>
        <span v-if="order.invoicetype == 3">增值税专用发票</span>
      </p>
    </div>
    <div class="orderCode">
      <p>订单编号：{{ orderNo }}</p>
      <p>下单时间：{{ order.odate }} {{ order.otime }}</p>
    </div>
    <div class="goodsInfo">
      <p class="title">一块医药</p>
      <div class="content" v-for="(item, index) in goods" :key="index">
        <img class="img" v-lazy="item.imgURl" />
        <!-- <p v-if="item.consell & 1 == 1" class="kongxiao" style="background-color: #ed2f26;">控销药</p> -->
        <div class="main">
          <p>{{ item.pname }} {{ item.pspec }}</p>
          <p class="compid">{{ item.manun }}</p>
          <div class="active">
            <p>单价:￥{{ item.pdprice }}</p>
            <p>小计:￥{{ (item.pdprice*100) * (item.pnum*100) / 10000 }}</p>
          </div>
        </div>
        <p class="count">
          <span>x{{ item.pnum }}</span>
          <van-icon v-show="!block[index]" name="arrow-down" @click="showBlock(index)" />
          <van-icon v-show="block[index]" name="arrow-up" @click="showBlock(index)" />
        </p>
        <div class="block" v-show="block[index]">
        <p>
          <span>余额抵扣</span>
          <span>优惠金额</span>
          <span>实付金额</span>
        </p>
        <p>
          <span>￥{{ item.balamt }}</span>
          <span>￥{{ item.distprice }}</span>
          <span>￥{{ item.payamt }}</span>
        </p>
      </div>
      </div>
      <!-- 赠品 -->
      <div v-if="goodsActiveInfo.length > 0" class="content">
        <img class="img" src="..\..\assets\z-logo.png" />
        <div class="main">
          <p>{{ goodsActiveInfo[0].pname }}</p>
          <div class="active flag">
            <p>小计:￥{{ goodsActiveInfo[0].pdprice }}</p>
          </div>
        </div>
        <p class="count">x{{ goodsActiveInfo[0].pnum }}</p>
      </div>
      <div v-if="mask" class="more" @click="showMore">
        <van-icon v-show="!icon" name="arrow-down" />
        <van-icon v-show="icon" name="arrow-up" />
      </div>
    </div>
    <div class="price">
      <p>
        <span>商品总价：</span>
        <span>￥{{ order.pdamt }}</span>
      </p>
      <p>
        <span>运费：</span>
        <span>￥{{ order.freight }}</span>
      </p>
      <p>
        <span>优惠金额：</span>
        <span>￥{{ order.distamt }}</span>
      </p>
      <p>
        <span>余额抵扣：</span>
        <span>￥{{ order.balamt }}</span>
      </p>
    </div>
    <div class="footer">
      <div class="acpay">
        <span>￥{{ order.payamt }}</span>
        <span>实付：</span>
      </div>
      <!-- 订单完成 -->
      <div class="bottom" v-if="order.ostatus == 3 || order.ostatus == 4">
        <van-icon name="ellipsis" @click="toggleTip" />
        <div class="btn">
          <button v-if="(order.ostatus == 3 || order.ostatus == 4) && order.asstatus!==200" @click="after">申请售后</button>
          <!-- <button v-if="order.ostatus == 4 && (order.cstatus&128) == 0" @click="evaluate">评价</button> -->
          <button v-if="order.ostatus >= 2 && order.ostatus != -4 && order.ostatus == 3" @click="logistics">查看物流</button>
          <button  v-if="order.ostatus == 3" @click="confirmReceipt">确认收货</button>
          <button v-if="order.ostatus == 4" @click="againShopCart">再次购买</button>
        </div>
      </div>
      <!-- 待付款 -->
      <div class="bottom" v-if="(order.ostatus == 0 && order.opayway == -1) && timer > 0">
        <span>
          <span>
            <span class="seckill">{{ seckill.m }}</span>
            分
            <span class="seckill">{{ seckill.s }}</span>
            秒
          </span>
        </span>
        <div class="btn">
          <button @click="cancelOrder">取消订单</button>
          <button @click="toPayment">立即付款</button>
        </div>
      </div>
      <!-- 线下到付 -->
      <div class="bottom" v-if="order.ostatus == 0 && order.opayway !== -1">
        <span>
        </span>
        <div class="btn">
          <button @click="transfer">转账说明</button>
          <button v-if="timer > 0" @click="cancelOrder">取消订单</button>
        </div>
      </div>
      <!-- 支付方式 -->
      <van-actionsheet
        class="payment"
        v-model="payment"
        :close-on-click-overlay="overlay"
      >
        <div
          v-for="(item, index) in payments"
          :key="index"
          @click="onSelectPay(index)"
        >
          <img :src="item.url" :alt="item.name" />
          <p class="name">{{ item.name }}</p>
        </div>
        <p class="cancel" @click="onCancel">稍后再付</p>
      </van-actionsheet>
      <!-- 待收货 -->
      <div class="bottom" v-if="order.ostatus == 2">
        <van-icon name="ellipsis" @click="toggleTip" />
        <div class="btn">
          <button @click="logistics">查看物流</button>
        </div>
      </div>
      <!-- 待发货 -->
      <div
        class="bottom"
        v-if="order.ostatus == 1 && (order.opayway == 4 || order.opayway == 5)"
        >
        <div></div>
        <div class="btn">
          <button>联系客服</button>
        </div>
      </div>
      <!-- 已取消的订单 -->
      <div class="bottom" v-if="order.ostatus == -4">
        <div></div>
        <div class="btn">
          <button @click="delOrder">删除订单</button>
          <button @click="againShopCart">再次购买</button>
        </div>
      </div>
      <!-- 补开发票 -->
      <div class="againCoupon" v-show="tip">
        <div class="content" @click="toSuppInvo">补开发票</div>
        <div class="mask"></div>
      </div>
    </div>
  </div>
</template>
<script>
import { Dialog } from 'vant';
export default {
  data() {
    return {
      head: true,
      orderNo: 0,
      order: {},
      timer: 0,
      seckill: {
        m: 0,
        s: 0
      },
      payment: false,
      payments: [
        { attr: 'wxpay', name: '微信支付', url: require("../../assets/pay/wx.png") },
        { attr: 'alipay', name: '支付宝支付', url: require("../../assets/pay/zfb.png") },
        { attr: 'xxpay', name: '线下支付', url: require("../../assets/pay/xx.png") }
      ],
      overlay: false,
      tip: false,
      goods: [],
      icon: false,
      mask: false,
      goodsActiveInfo: [],
      block: [],
    };
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    }
  },
  created() {
    //  获取订单号
    this.orderNo = this.$route.query.orderNo;
    this.getOrder();
    if(this.$route.query.back == 1) {
      this.head = true
    }else{
      this.head = false
    }
    this.getLogisticsInfo()
  },
  methods: {
    showBlock(i) {
      if(this.block[i]){
        this.$set(this.block, i, false)
      }else{
         this.$set(this.block, i, true)
      }
    },
    toSuppInvo() {
        //补开发票
        this.$router.push({
          path: "/patchInvoice",
          query: {
            orderNo: this.orderNo
          }
        });
      },
    //  获取订单信息
    getOrder() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
        cls: "OrderInfoModule",
        method: "getOrderDetail",
        params: [this.userInfo.comp.storeId, this.orderNo],
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            this.goodsActiveInfo = result.data.gifts;
            this.order = result.data.goods[0];
            this.order.goods.forEach(arr => {
              arr.sku = arr.pdno;
            });
            this.$request_img({
              context: this,
              arr: this.order.goods
            });
            if(this.order.goods.length > 2){
              this.mask = true
            }else{
              this.mask = false
            }
            this.goods = this.order.goods.slice(0,2)
            this.secondKill();
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    //  展示更多商品
    showMore() {
      if(this.goods.length === 2){
        this.goods = this.order.goods
        this.icon = true
      }else{
        this.goods = this.goods.slice(0,2)
        this.icon = false
      }
    },
    toggleTip() {
      this.tip = !this.tip
    },
    //  支付状态
    payText(type) {
      var text = "";
      switch (type) {
        case 0:
          text = "余额支付";
          break;
        case 1:
          text = "线上支付(微信)";
          break;
        case 2:
          text = "线上支付(支付宝)";
          break;
        case 3:
          text = "银联支付";
          break;
        case 5:
          text = "线下到付";
          break;
        case 4:
          text = "线下转账";
          break;
      }
      return text;
    },
    //  查看物流
    logistics() {
      this.$router.push({
        path: '/logistics',
        query: {
          orderNo: this.orderNo
        }
      })
    },
    evaluate() {
      this.$router.push({
        path: '/evaluate',
        query: {
          orderNo: this.orderNo
        }
      })
    },
    after() {
      this.$router.push({
        path: '/applyAfter',
        query: {
          orderNo: this.orderNo
        }
      })
    },
    //  倒计时
    secondKill() {
      let date = this.order.odate + " " + this.order.otime;
        date = date.substring(0,19);
        date = date.replace(/-/g,'/');
        var timestamp = new Date(date).getTime();
        var timestampNow = parseInt(new Date().getTime());    // 当前时间戳
        var times = Math.floor((timestampNow - timestamp) / 1000);
        var i = 0
        if(this.order.opayway == 4){
          i = 1800
        }else{
          i = 900
        }
        var thirtyMin = i;
        this.timer = thirtyMin - times;
        this.seckill.m = Math.floor(this.timer / 60) % 60;
        this.seckill.s = this.timer % 60;
        var timeOut = setTimeout(this.secondKill, 1000);
        if (this.timer < 0) {
          window.clearTimeout(timeOut)
        }
    },
    //  删除订单
    delOrder() {
      Dialog.confirm({
        title: '提示',
        message: '确定要删除此订单吗？'
      }).then(() => {
        JNB.query({
          serverName: "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: "OrderOptModule",
          method: "deleteOrder",
          params: {
            orderno: this.orderNo,
            cusno: this.$loadUserInfo().comp.storeId
          },
          callback: data => {
            let result = JSON.parse(data);
            if (result.code === 200) {
              if(this.head){
                this.$router.go(-1)
              }else{
                this.$router.replace({
                  path: '/shoppingCart'
                })
              }
            } else {
              this.$toast.fail(result.message);
            }
          }
        });
      }).catch(() => {
        // on cancel
      });
    },
    //  获取物流信息
    getLogisticsInfo() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
        cls: "OrderOptModule",
        method: "getLogisticsInfo",
        params: {
          orderno: this.orderNo,
          cusno: this.$loadUserInfo().comp.storeId
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    //  选择支付方式
    onSelectPay(item) {
      if(item == 0 || item == 1 ) {
        JNB._requestNative(this.payments[item].attr,JSON.stringify({orderno: this.orderNo}),(data)=>{
          if (data == 0){
            if(this.$store.state.payResult){
              Dialog.alert({
                title: '温馨提示',
                message: '支付成功！'
              }).then(() => {
                this.getOrder();
              });
            }
          }else{
            Dialog.alert({
              title: '支付失败',
              message: '请到订单详情页继续完成支付~'
            }).then(() => {
              this.getOrder();
            });
          }
        })
      }else if(item == 2) {
        Dialog.confirm({
          title: '您确认付款方式为线下转账？',
          message: `一、请您在汇款时备注一块医药订单编号，这将会很大程度上缩短我们的核款时间并能尽快为您安排发货。<br>二、请于24小时内汇款并确保汇款金额与订单总金额一致，到账时间为1-3个工作日。<br>三、从下单之日起5天内如果还未付款并到账，系统将自动取消该订单。<br>四、我们将在收到款项后及时确认收款并安排发货，若款项汇出2天后未帮您收款确认，请及时拨打底部客服热线。`
        }).then(() => {
          JNB.query({
            serverName: "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
            cls: "PayModule",
            method: "offlinePay",
            params: {
              orderno: this.orderNo,
              compid: this.$loadUserInfo().comp.storeId,
              paytype: 4
            },
            callback: data => {
              let result = JSON.parse(data);
              if (result.code === 200) {
                this.getOrder();
              } else {
                this.$toast.fail(result.message);
              }
            }
          });
        }).catch(() => {
          // on cancel
        });
      }
      this.payment = false
    },
    //  取消支付
    onCancel() {
      // confirm('请尽快完成支付哟~')
      this.payment = false
    },
    toPayment() {
      this.payment = true
    },
    //  取消订单
    cancelOrder() {
      Dialog.confirm({
        title: '温馨提示',
        message: '订单取消后将无法恢复，优惠券将不再返还，支付优惠也将一并取消，请慎重！'
      }).then(() => {
        JNB.query({
          serverName: "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: "TranOrderOptModule",
          method: this.order.opayway == 4 ? 'cancelOffLineOrder' : 'cancelOrder',
          params: {
            orderno: this.orderNo,
            cusno: this.$loadUserInfo().comp.storeId
          },
          callback: data => {
            let result = JSON.parse(data);
            if (result.code === 200) {
              Dialog.alert({
                message: '订单取消成功！'
              }).then(() => {
                this.getOrder();
              });
            } else {
              this.$toast.fail(result.message);
            }
          }
        });
      }).catch(() => {
        // on cancel
      });
    },
    // 再次购买
    againShopCart() {
      // let obj = this.order.goods.map(arr => {
      //   return {
      //     compid: this.$loadUserInfo().comp.storeId,
      //     pdno: arr.pdno,
      //     pnum: arr.pnum,
      //     checked: 0
      //   }
      // })
      let obj = {
        orderno: this.order.orderno,
        compid: this.$loadUserInfo().comp.storeId
      }
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
        cls: "ShoppingCartModule",
        method: "againShopCart",
        params: obj,
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            this.$router.push({
              path: '/shoppingCart',
              query: {
                againShopping: true
              }
            })
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    //  确认收货
    confirmReceipt() {
      Dialog.confirm({
        title: '提示',
        message: '是否确认签收？'
      }).then(() => {
        JNB.query({
          serverName: "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: "OrderOptModule",
          method: "confirmReceipt",
          params: {
            orderno: this.orderNo,
            cusno: this.$loadUserInfo().comp.storeId
          },
          callback: data => {
            let result = JSON.parse(data);
            if (result.code === 200) {
              Dialog.alert({
                message: '订单签收成功！'
              }).then(() => {
                this.getOrder();
              });
            } else {
              this.$toast.fail(result.message);
            }
          }
        });
      }).catch(() => {
        // on cancel
      });
      
    },
    //  转账说明
    transfer() {
      this.$router.push({
        path: '/transferExplain'
      })
    }
  }
};
</script>
<style scoped>
p {
  margin: 0;
  padding: 0;
}
.successfulTrade {
  padding: 66px 0 150px;
  box-sizing: border-box;
}
.successfulTrade .state {
  font-size: 30px;
  font-weight: bold;
  color: rgba(51, 51, 51, 1);
  text-align: center;
  padding: 40px 0 10px;
}
.successfulTrade .userInfo,
.successfulTrade .orderInfo,
.successfulTrade .orderInfo_D,
.successfulTrade .orderMode,
.successfulTrade .orderCode,
.successfulTrade .goodsInfo,
.successfulTrade .price {
  width: 710px;
  background: rgba(255, 255, 255, 1);
  border-radius: 8px;
  margin: 20px auto;
  box-shadow:0px 4px 6px 0px rgba(153,153,153,0.3);
}
.successfulTrade .userInfo .userName {
  padding: 30px 0 0;
}
.successfulTrade .userInfo .userName span:first-child {
  font-size: 32px;
  color: rgba(51, 51, 51, 1);
  margin: 0 20px 0 100px;
  font-weight: bold;
}
.successfulTrade .userInfo .userName .tel {
  font-size: 28px;
  color: rgba(153, 153, 153, 1);
}
.successfulTrade .userInfo .userName .van-icon {
  float: right;
  margin: 10px 20px 0 0;
  font-size: 28px;
  font-weight: bold;
  color: rgba(153, 153, 153, 1);
}
.successfulTrade .userInfo .storeName {
  font-size: 28px;
  color: rgba(51, 51, 51, 1);
  margin: 20px 100px 0;
}
.successfulTrade .userInfo .address {
  overflow: hidden;
  padding-bottom: 20px;
}
.successfulTrade .userInfo .address img {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  padding: 20px;
  float: left;
}
.successfulTrade .userInfo .address p {
  width: 550px;
  height: 96px;
  font-size: 28px;
  color: rgba(51, 51, 51, 1);
  float: left;
  margin-left: 3px;
  display: flex;
  align-items: center;
}
.successfulTrade .orderInfo .content {
  display: flex;
  align-items: center;
  justify-content: space-between;
